export function getRandomInt(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

export const timerSelfDestroy = (delay, cb) => {
    const timeout = setTimeout(() => {
        cb();
        clearTimeout(timeout);
    }, delay);
}